body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    width: 100%;
    background: -webkit-linear-gradient(top, rgba(0,0,0,0.5), rgba(255,255,255,0.2)), url(/Mountain_Kingdom/static/media/background.c8e81804.jpg);
    background: linear-gradient(top, rgba(0,0,0,0.5), rgba(255,255,255,0.2)), url(/Mountain_Kingdom/static/media/background.c8e81804.jpg);
    background-position: center;
    background-size: cover;
    position: relative;
}

html {
    font-size: 14px;
}


@media (max-width: 985px) {

    html {
        font-size: 12px;
    }
}


@media (max-width: 850px) {

    html {
        font-size: 11px;
    }
}


@media (max-width: 750px) {

    html {
        font-size: 10px;
    }
}


@media (max-width: 650px) {

    html {
        font-size: 9px;
    }
}

@media (max-width: 600px) {

    html {
        font-size: 8px;
    }

}

nav {
    position: absolute;
    width: 100%;
    top: 5%;
    letter-spacing: 0.2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.mobile_nav {
    display: none;
}

ul {
    height: 3.5rem;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    text-transform: uppercase;
}

h1 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.fa-circle {
     font-size: 1.5rem;
     color: white;
 }


ul li {
    width: 20%;
    display: inline-block;
    list-style: none;
    cursor: pointer;
}

.li_right {
    text-align: right;
}



@media (max-width: 550px) {

    ul.nav {
        height: 50px;
        width: 50px;
        background-color: grey;
    }

    .mobile_nav {
        height: 40px;
        width: 40px;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-flex-direction: column;
                flex-direction: column;
        position: absolute;
        cursor: pointer;
        right: 5%;
        top: 10%;
        padding: 10px 5px 10px 5px;
        box-sizing: border-box;
        background: rgba(0, 0, 0, 0.4);
        box-shadow: 8px 10px 20px 0 rgba(46, 61, 73, .15);
    }

    .mobile_nav div {
        border-radius: 50px;
        width: 80%;
        height: 3px;
        background-color: white;
        box-shadow: 8px 10px 20px 0 rgba(46, 61, 73, .15);
    }

    nav ul {
        display: none;
    }

}

main {
    height: 78vh;
    width: 100%;
    color: whitesmoke;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

main section {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

h2 {
    font-family: 'Lobster', cursive;
    font-size: 3.7rem;
    margin-bottom: 3.7rem;
    font-weight: normal;
}

.login_btn {
    height: 2.3rem;
    width: 15rem;
    font-size: 1rem;
    letter-spacing: 0.2rem;
    cursor: pointer;
    border: none;
    border-radius: 50px;
    text-transform: uppercase;
    color: white;
    box-shadow: 8px 10px 20px 0 rgba(46, 61, 73, .15);
    background: rgb(221, 190, 92);
    background: -webkit-linear-gradient(left, rgba(221, 190, 92, 1) 22%, rgba(205, 166, 40, 1) 73%);
    background: linear-gradient(90deg, rgba(221, 190, 92, 1) 22%, rgba(205, 166, 40, 1) 73%);
}


@media (max-width: 650px) {

    h2 {
        font-family: 'Lobster', cursive;
        font-size: 4.2rem;
    }

    .login_btn {
        height: 3rem;
        width: 20rem;
        font-size: 1.4rem;
    }
}


@media (max-width: 600px) {

    h2 {
        font-family: 'Lobster', cursive;
        font-size: 4.2rem;
    }

    .login_btn {
        height: 3rem;
        width: 20rem;
        font-size: 1.4rem;
    }
}



@media (max-width: 320px) {

    main {
        height: 90%;
        color: whitesmoke;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    main section {
        width: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }

    h2 {
        font-family: 'Lobster', cursive;
        font-size: 30px;
        letter-spacing: 3px;
        margin-bottom: 15px;
    }

    .login_btn {
        height: 20px;
        width: 40%;
        font-size: 7px;
        letter-spacing: 1px;
        cursor: pointer;
        border: none;
        border-radius: 50px;
        text-transform: uppercase;
        color: white;
        box-shadow: 8px 10px 20px 0 rgba(46, 61, 73, .15);
        background: rgb(221, 190, 92);
        background: -webkit-linear-gradient(left, rgba(221, 190, 92, 1) 22%, rgba(205, 166, 40, 1) 73%);
        background: linear-gradient(90deg, rgba(221, 190, 92, 1) 22%, rgba(205, 166, 40, 1) 73%);

    }
}


footer {
    height: 10%;
    width: 100%;
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    bottom: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: whitesmoke;
    font-size: 1rem;
    padding: 0 6rem 0 6rem;
    box-sizing: border-box;
}

.copy_rights {
    text-transform: uppercase;
    letter-spacing: 1px;
}

.privacy_container {
    letter-spacing: 0.4rem;
}

.designed_by_author {
    font-weight: bold;
    letter-spacing: 1px;

}

@media (max-width: 320px) {

    footer {
        height: 30px;
        width: 100%;
        background: rgba(0, 0, 0, 0.7);
        position: absolute;
        bottom: 0;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        color: whitesmoke;
        font-size: 7px;
        padding: 0 10px 0 10px;
        box-sizing: border-box;
        font-family: 'Josefin Sans', sans-serif;
    }

    .copy_rights {
        text-transform: uppercase;
    }

    .privacy_container {
        letter-spacing: 0.5px;
    }

    .designed_by_author {
        font-weight: bold;
    }

}


.login_subpage {
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 2;
}

.login_window {
    height: 18rem;
    width: 17rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: white;
    padding: 40px;
    box-shadow: 8px 10px 20px 0 rgba(46, 61, 73, .15);
}

.login_window .login_window__header {
    letter-spacing: 0.1rem;
    font-size: 1.1rem;
}

input {
    width: 100%;
    border: none;
    box-sizing: border-box;
    text-align: center;
    padding: 7px;
    border-bottom: 1px solid black;
    outline: none;
    margin: 15px 0 15px 0;
}





